import React from "react"
import { graphql } from "gatsby"
import HomepageComponent from "../../gatsby-theme-minimal-blog/components/homepage"

const Homepage = ({ data }) => {
  const mergedPosts = data.allPost.nodes.map((post, index) => {
    const { frontmatter } = data.allMdx.nodes.find(fm => post.slug === fm.frontmatter.slug);

    return {
      ...post,
      frontmatter
    }
  })
  return <HomepageComponent posts={mergedPosts} />
}

export default Homepage

export const query = graphql`
  query($formatString: String!) {
    allPost(sort: { fields: date, order: DESC }, limit: 25) {
      nodes {
        slug
        title
        date(formatString: $formatString)
        excerpt
        timeToRead
        description
        tags {
          name
          slug
        }
      }
    }
    allMdx(sort: { fields: frontmatter___date, order: DESC }, limit: 25 ) {
      nodes {
        slug
        frontmatter {
          date
          slug
          author
          email
        }
      }
    }
  }
`